  justify-content: center;
  flex-wrap: wrap; }

.flats__item {
  width: 270px;
  border-radius: 100px;
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 0 15px 40px; }
  .flats__item--hidden {
    display: none; }

.flats__image {
  display: block;
  height: 250px;
  width: 270px;
  border-radius: 10px 10px 0 0; }

.flats__description {
  background-color: #ffffff;
  border-radius: 0 0 10px 10px;
  padding: 15px 15px 10px; }

.flats__item-title {
  margin: 0 0 15px;
  line-height: 20px;
