  left: 10px; }

.flats__comfort-label {
  width: 78px;
  left: 97px; }

.flats {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  padding: 30px 0 45px;
  justify-content: center; }

.flats__wrap {
  max-width: 900px;
  display: flex;
  flex-direction: column;
  align-items: center; }

.flats__show-more-items {
  border: 0;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  outline: none;
  background-color: #ffffff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
