  .flats__show-more-items--hidden {
    display: none; }

.flats__show-more-items:hover,
.flats__show-more-items:focus {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4); }

.flats__show-more-items:active {
  opacity: 0.7; }

.flats__button-wrap {
  padding: 0 15px;
  margin-top: -18px;
  width: 100%; }

.flats__show-more-slogan {
  line-height: 20px;
  font-weight: 700; }

.flats__plus-wrap {
  width: 40px;
  height: 40px;
  border-radius: 50%;
