  justify-content: center;
  align-items: center; }

@media (max-width: 768px) {
  .flats {
    padding-top: 0;
    flex-direction: column-reverse; }
  .flats__item {
    margin: 0 10px 25px;
    width: 280px; }
  .flats__image {
    width: 280px; }
  .flats__button-wrap {
    margin-top: 0; }
  .flats__show-more-items {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 15px; }
  .flats__plus-wrap {
