.filter {
  padding: 15px; }

.filter__submit-button {
  border: 0;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  outline: none;
  width: 270px;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  background-color: #f03b8c;
  color: #ffffff;
  font-weight: 500;
  margin-bottom: 30px;
  border-radius: 2px;
  padding: 13px 5px 15px;
  box-shadow: none;
  opacity: 1;
  transition: all 0.3s ease; }

.filter__submit-button:hover,
.filter__submit-button:focus {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4); }

.filter__submit-button:active {
  opacity: 0.7; }

.filter__reset-button {
  border: 0;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  outline: none;
  width: 270px;
  border-radius: 2px;
  border: 1px solid #5f5f5f;
  color: #5f5f5f;
  padding: 10px;
  display: flex;
