  justify-content: center;
  box-shadow: none;
  opacity: 1;
  transition: all 0.3s ease; }

.filter__reset-button:hover,
.filter__reset-button:focus {
  border-color: #111111;
  color: #111111;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4); }

.filter__reset-button:active {
  opacity: 0.7; }

.filter__reset-slogan {
  line-height: 34px;
  margin-right: 8px; }

.filter__menu-button {
  border: 0;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  outline: none;
  color: #111111;
  line-height: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  font-weight: 700;
  margin-bottom: 25px;
  opacity: 1;
  transition: all 0.3s ease; }
  .filter__menu-button--active {
    margin-bottom: 0; }

.filter__menu-button:hover,
.filter__menu-button:focus {
  color: #5f5f5f; }

.filter__menu-button:active {
  opacity: 0.7; }

.filter__item {
  margin-bottom: 43px; }
  .filter__item--terms .filter__menu-button {
    margin-bottom: 23px; }
  .filter__item--additional-options {
    margin-bottom: 22px; }

.filter__options-wrap,
.filter__terms-list,
.filter__distance-list {
  overflow: hidden;
  transition: all 0.3s ease-out; }

.filter__service-label {
  position: relative;
  padding-left: 48px;
  cursor: pointer; }

.filter__service-label:hover {
  color: #5f5f5f; }

.filter__service-label::after {
  width: 40px;
  height: 20px;
  border-radius: 50px;
  display: block;
  background-color: #f03b8c;
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%); }

.filter__service-label::before {
  position: absolute;
  content: "";
  border-radius: 50%;
  background-color: #ffffff;
  width: 16px;
  height: 16px;
  border: 1px solid #ffffff;
  top: 50%;
  transform: translateY(-50%);
  left: 3px;
  transition: left 0.5s ease;
  z-index: 2; }

.filter__service-input:checked + .filter__service-label::before {
  left: 21px; }

.filter__service-label:hover::before {
  border-color: #111111; }

.filter__service-input:hover + .filter__service-label::before,
.filter__service-input:focus + .filter__service-label::before {
  border-color: #111111; }

.filter__service-input:active + .filter__service-label {
  opacity: 0.7; }

.filter__service-item {
  margin-bottom: 30px; }

@media (max-width: 768px) {
  .filter {
    padding: 10px 10px 30px; }
  .filter__menu-button {
    margin-bottom: 15px; }
    .filter__menu-button--active {
      margin-bottom: 0; }
  .filter__item {
